import './dashboardClienteOrçamento.css'
import Navbar from '../../components/Navbar'
import { BsTrash } from 'react-icons/bs'
import { BsPencil } from 'react-icons/bs'
import axios from 'axios';
import { useState, useEffect } from 'react';
import { parseJwt } from '../../services/auth';
import Alert from 'react-bootstrap/Alert'


export default function InfoCarro() {


  //Plate, Color, Year, City, Model, Brand, 


  // VARIÁVEIS PARA CADASTRO DE CONSULTAS
  const [Plate, setPlate] = useState("")
  

  const [Color, setColor] = useState("")
 

  const [Year, setYear] = useState("")
 
  const [City, setCity] = useState("")
  

  const [Model, setModel] = useState("")
  

  const [Brand, setBrand] = useState("")
  
  const[erroMensagem, setErroMensagem] = useState("")




  // setStates para a listagem das informaçoes do veiculo
  const [infoCarro, setCarro] = useState([])


  // buscar os carros do usuário
 function limparCampos(){
  setPlate("")
  setColor("")
  setYear("")
  setCity("")
  setModel("")
  setBrand("")
 }

  // Funcionando
  function PostInfoCarro(event) {

      event.preventDefault()

      axios.post('http://localhost:5000/api/Car', {

          plate: Plate,
          color: Color,
          year: Year,
          city: City,
          model: Model,
          brand: Brand,
          idUser: parseJwt().jti


      }, {
          headers: {
              'Authorization' : 'Bearer ' + localStorage.getItem('usuario-login')
          }
      })
          .then(resposta => {
              if (resposta.status === 200) {
                  setErroMensagem("informações do carro cadastrada!")
                  GetCarros();
                  limparCampos()
              }
          })
          .catch(erro => {
              console.log(erro)
              setErroMensagem("Carro não cadastrado!")
          })



  };

 

  function deleteCar(cars){
    axios.delete('http://localhost:5000/api/car/ ' + cars.idCar, {
      headers : {
        'Authorization' : 'Bearer ' + localStorage.getItem('usuario-login')
      }
    })

    .then(response => {
      if(response.status === 202){
        GetCarros();
      }
    })

    .catch((erro) => {console.log(erro)})
  }


  
   async function GetCarros() {
      await axios.get('http://localhost:5000/api/Car/mycar/ ' + parseJwt().jti , {
        headers : {
          'Authorization' : 'Bearer ' + localStorage.getItem('usuario-login')
        }
      })
          .then(response => {
              if (response.status === 200) {
                  setCarro(response.data)
                  setErroMensagem("Veículo listado com sucesso!")
              }
          })
          .catch((erro) => console.log(erro))
  }

  useEffect(() => {GetCarros() }, [{}])
  // funções para ciclos de vida 

  return(
    <div>
      <Navbar />
      <div className="dashboard">
          <h2 className="titulo">Informações do carro</h2>
                <div className="dashboard_fonte">
                   
                </div>
                <form onSubmit={PostInfoCarro}>
                  <div className="inputs">
                    <div className="linha-input-1">
                      <input type="text" name={Plate} value={Plate} onChange={(event) => {setPlate(event.target.value)}} placeholder="Placa do carro: "  required/>
                      <input type="text" name={Model} value={Model} onChange={(event) => {setModel(event.target.value)}} placeholder="Modelo: " required/>
                    </div>
                    <div className="linha-input-1">
                      <input type="text" name={Brand} value={Brand} onChange={(event) => {setBrand(event.target.value)}} placeholder="Marca: " required />
                      <input type="text" name={Color} value={Color}onChange={(event) => {setColor(event.target.value)}} placeholder="Cor: "  required />
                    </div>
                    <div className="linha-input-1">
                      <input type="text" name={Year} value={Year} onChange={(event) => {setYear(event.target.value)}} placeholder="Ano do modelo: " required />
                      <input type="text" name={City} value={City} onChange={(event) => {setCity(event.target.value)}} placeholder="Município: "  required/>
                    </div>
                    <button className="btnAdd" type="submit">ADICIONAR</button>
                  </div>
                </form>
           <div className="dashboard_fonte">
            <h2 className="titulo">Seus Carros</h2>
          </div> 
          {
            infoCarro.map((cars) => {
              return(
                <div className="lista" key={cars.idCar}>
                <p>Placa: {cars.plate}</p>
                <p>Marca: {cars.brand}</p>
                <p>Modelo: {cars.model}</p>
                <p>Cor: {cars.color}</p>
                <p>Ano: {cars.year}</p>
                <p><BsTrash onClick={() => deleteCar(cars)} style={{cursor : "pointer"}} size={25} /></p>
              </div>
              )
            })
          }
          
              
      </div>
    </div>
  )
}