import './cadastro.css'
import cadastro1 from "../../assets/cadastro.png"
import axios from 'axios';
import { Form } from 'react-bootstrap';
import React, {useState} from 'react';
import { parseJwt } from '../../services/auth';
import { useHistory } from 'react-router-dom';
import Alert from 'react-bootstrap/Alert'



export default function Cadastro() {

    //   state = {
    //     selectedFile : null,

    //   }
    // //Name, Email, Password, Phone e Image
    // fileSelectedHandler = event => {
    //     this.setState({

    //       selectedFile : event.target.files[0]
    //     })

    //     }

    //     fileUploadHandler = () => {
    //       const fd = new FormData();
    //       fd.append('image', this.state.selectedFile, this.state.selectedFile.name);
    //     axios.post('http://localhost:5000/api/User/create-account', fd)
    //     .then(res => {
    //       console.log(res);
    //     });
    //     }
   //Name, Email, Password, Phone e Image


    // VARIÁVEIS PARA CADASTRO DE CONSULTAS
    const [Name, setName] = useState("")
    console.log(Name)

    const [Email, setEmail] = useState("")
    console.log(Email)

    const [Password, setPassword] = useState("")
    console.log(Password)

    const [Phone, setPhone] = useState("")
    console.log(Phone)

    const [Image, setImage] = useState("")
    console.log(Image)

    const[Plate, setPlate] = useState("")

    const[TypeUser, setTypeUser] = useState(2)

    const history = useHistory()

    async function handleSubmit(event) {
      event.preventDefault();
    
      const formData = new FormData();
    
      formData.append('name', Name);
      formData.append('email', Email);
      formData.append('password', Password);
      formData.append('phone', parseInt(Phone));
      //formData.append('imagePlate', Image);
      formData.append('plate', Plate)
      formData.append('typeUser', TypeUser)
    
      try {
        await axios.post('http://localhost:5000/api/User/create-account', {
          name : Name,
          email : Email,
          password : Password,
          phone : Phone,
          imagePlate : Image,
          plate : Plate,
          typeUser : TypeUser
        })
        
        .then(response => {
          if(response.status === 200){
            console.log('Usuário cadastrado com sucesso!')
            history.push("/login")
          }
        })

        } catch (error) {
        throw new Error(error.message)
      }
    }

  return (
    <div className="Cadastro">



      <div className="cadastro__Container3">
      <div className="separador"></div>
          <h1>cadastre-se</h1>
          <div className="separador">

          </div>
        <form className="formCadastro" onSubmit={handleSubmit}>

          <input
            placeholder="Nome"
            className='cadastro__input'
            type='text'
            name='name'
             value={Name}
             onChange={(event) => {setName(event.target.value)}}
           
          />

          <input
            placeholder="Email"
            className='cadastro__input'
            type='email'
            name='email'
            value={Email}
            onChange={(event) => {setEmail(event.target.value)}}

          />

          <input
            placeholder="Senha"
            type='password'
            name='password'
            className='cadastro__input'
            value={Password}
            onChange={(event) => {setPassword(event.target.value)}}

          />

          <input
            placeholder="Telefone/Celular: (xx) 99999-9999"
            className='cadastro__input'
            type='text'
            name='phone'
           value={Phone}
           onChange={(event) => {setPhone(event.target.value)}}

          />

            <input
            placeholder="Imagem"
            className='cadastro__input'
            type='file'
            name='imagePlate'
            value={Image}
            
            onChange={(event) => {setImage(event.target.files[4])}}
          />

            <h4 style={{marginTop : 30}} >OU</h4>

            <input
            placeholder="Insira a placa do seu veículo"
            className='cadastro__input'
            type='text'
            name='plate'
            value={Plate}
            onChange={(event) => {setPlate(event.target.value)}}
          /> 


          <button type="submit" className='cadastro__botao'>
            CADASTRE-SE
          </button>
          </form>

          <div className="separador"></div>
      </div>

      <div className="separador"></div>

     
      <div className="cadastro_Container4">
          <p className="cadastro_p">Tem uma conta?

          <a href="/login" tabIndex="0">Entre no sistema</a>
          
          </p>

      </div>
     
    
      
    </div>




  );
}

