import Navbar from "../../components/Navbar";
import './dashboardClienteOrçamento.css';
import { Modal, Button }  from  'react-bootstrap'
import { useState } from "react";
import Alert from 'react-bootstrap/Alert'

function DashboardClienteOrçamento() {
    
    const [show, setShow] = useState(false);
    
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    
    return (
        <div >
            <Navbar />
            <div className="dashboard">
                <div className="dashboard_fonte">
                    <h2 className="titulo">PRÉ-ORÇAMENTOS </h2>
                    <p>* Pré-orçamentos gerados com base em problemas recorrentes  em veículos da mesma marca e modelo que o seu.</p>
                </div>
               
                <div className="card-orcamento" onClick={handleShow}>
                    <h2 className="subtitulo">Serviços para seu carro </h2>
                    <div className="bar"></div>
                </div>

                <Modal show={show} onHide={handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title><p className="p-title-modal">Problemas comuns</p></Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="line-car">
                            <p><p style={{fontWeight : 'bold'}}>Problema:</p>Porta amassada: </p>
                            <p><p style={{fontWeight : 'bold'}}>Valor:</p> ~ R$ 200</p>
                        </div>
                        <div className="line-car">
                            <p>Para-choque amassado: </p>
                            <p> ~ R$ 320</p>
                        </div>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button className="btnAdd2" variant="secondary" onClick={handleClose}>
                            Close
                        </Button>
                    </Modal.Footer>
                    </Modal>

                   
            </div>
        </div>
    )
}

export default DashboardClienteOrçamento;
